<template>
    <div class='book-wrapper'>
        <el-row type="flex" :gutter='20'>
            <el-col :span='8'>
                <rank-list :type='1' title='' @click='handleClick("wanna","")'></rank-list>
            </el-col>
            <el-col :span='16'>
                <h3 class='wrapper-title'>
                    <span></span>
                    <el-date-picker
                        v-model='value'
                        type="daterange"
                        align="right"
                        unlink-panels
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        :picker-options="pickerOptions"
                        size="mini"
                        @change='pickDate'
                        value-format="yyyyMMdd"
                        
                        >
                    </el-date-picker>
                </h3>
                <div id='active-chart' class="active-chart"></div>
            </el-col>
        </el-row>
        <el-row type="flex" :gutter='20'>
            <el-col :span='8'>
                <rank-list :type="3" title='' @click='handleClick("like", "")'></rank-list>
            </el-col>
            <el-col :span='8'>
                <rank-list :type="4" title='' @click='handleClick("score", "")'></rank-list>
            </el-col>
            <el-col :span='8'>
                <rank-list :type="5" title='' @click='handleClick("comment", "")'></rank-list>
            </el-col>
        </el-row>
        <el-dialog :title='dialogTitle' :visible.sync="dialogFormVisible" width="640px" @open='handleOpen'>
            <el-tabs type="border-card" class='list-card' v-model='selected'>
                <el-tab-pane label="" name='week'>
                    <rankview-list ref='week'></rankview-list>
                </el-tab-pane>
                <el-tab-pane label="" name='month'>
                    <rankview-list ref='month'></rankview-list>
                </el-tab-pane>
                <el-tab-pane label="" name='year'>
                    <rankview-list ref='year'></rankview-list>
                </el-tab-pane>
            </el-tabs>
        </el-dialog>
    </div>
</template>
<script>
import rankList from '@/components/RankList';
import rankviewList from '@/components/RankviewList';
import echarts from "echarts";
import moment from 'moment';

export default {
    name: "bookStatistics",
    components: {
        rankList,
        rankviewList
    },
    data() {
        return {
            //data: [],
            //,
            value: [new Date(moment().isoWeekday(1)), new Date(moment().isoWeekday(7))],
            //
            pickerOptions: {
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        const start = new Date(moment().isoWeekday(1));
                        const end = new Date(moment().isoWeekday(7));
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const start = new Date(moment().year(),moment().month(),1);
                        const end = new Date(moment().year(),moment().month(),moment().daysInMonth());
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const start = new Date(moment().year(), 0, 1);
                        const end = new Date(moment().year(), 11, 31);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
            dialogFormVisible: false, ////dialog
            selected: 'week', //
            chartData: {},  //chart
            option: {}, //echarts option
            myChart: null, //echarts
            moreType: '', //
            dialogTitle: '' //dialog title
        }
    },
    mounted() {
        this.$nextTick(() => {
            let time = [
                moment(this.value[0]).format('YYYYMMDD'),
                moment(this.value[1]).format('YYYYMMDD'),
            ]
            this.myChart = echarts.init(document.getElementById('active-chart'));
             this.getData(time)
            .then((res) => {
                this.chartData = res.data;
                this.setChartOption();
                this.myChart.setOption(this.option);
            })
        });
    },
    methods: {
        //
        getData(value) {
            return this.$axios.get('/api/count/login', {
                params: {
                    beginDate: value[0],
                    endDate: value[1]
                }
            })
        },
        //
        pickDate(val) {
            this.getData(val)
            .then((res) => {
                this.chartData = res.data;
                this.setChartOption();
                this.myChart.setOption(this.option);
            })
        },
        //
        handleClick(type, title) {
            this.dialogFormVisible = true;
            this.moreType = type;
            this.dialogTitle = title;
        },
        //dialog
        handleOpen() {
            this.selected = 'week';
            let options = {
                wanna: {
                    year: 1,
                    month: 6,
                    week: 11 
                },
                like: {
                    year: 3,
                    month: 8,
                    week: 13
                },
                score: {
                    year: 4,
                    month: 9,
                    week: 14
                },
                comment: {
                    year: 5,
                    month: 10,
                    week: 15
                }
            }
            this.$nextTick(() => {
                this.$refs['week'].initData(options[this.moreType].week);
                this.$refs['month'].initData(options[this.moreType].month);
                this.$refs['year'].initData(options[this.moreType].year);
            })
            
        },
        //echarts option
        setChartOption() {
            this.option = {
                tooltip : {
                    trigger: 'item'
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.chartData.date
                },
                yAxis: {
                    type: 'value'
                },
                series: [{
                    data: this.chartData.count,
                    type: 'line',
                    areaStyle: {}
                }]
            };
        }
    }
}
</script>
<style lang="scss" scoped>
.el-row + .el-row {
    margin-top: 20px;
}
.el-row {
    align-items: stretch;
}
.book-wrapper {
    padding: 0 10px;
    h3.wrapper-title {
        font-size: 18px;
        font-weight: 400;
        line-height: 40px;
        overflow: hidden;
        height: 40px;
        padding-bottom: 4px;
        color: #1a1a1a;
        border-bottom: 1px solid $Mercury;
        position: relative;
        background-color: $White;
        .el-date-editor {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY((-50%));
        }
    }
    .active-chart {
        width:100%;
        height: calc(100% - 50px);
        background-color: $White;
    }
}
</style>
